.close-span {
    position: absolute;
    top: 50%;
    right: 10px;
    transform: translateY(-50%);
    cursor: pointer; }



.gallery {
    display: grid;
    grid-template-columns: 200px 1fr;
    h1 {
        grid-column: 1/-1;
        justify-self: center; }
    &__big-image {
        grid-column: 2/3;
        img {
            width: 100%;
            height: 100%; } }


    &__list {
        margin: 0;
        padding: 0;
        list-style-type: none;
        display: grid;
        grid-template-rows: repeat(5,1fr );
 }        // margin-top: 20px
    &__item {
        // margin: -2px
        img {
            width: 100%;
 } } }            // max-height: 100px
.active {
    border: 2px solid white;
    filter: sepia(45%); }

// Slider styles
.slider {
    width: 600px;
    margin: 0 auto;
    border: 1px solid #000;
    overflow: hidden;
    display: grid;
    grid-template-rows: 1fr auto;
    &__container {
        display: flex;

 }        // transform: translateX(-100px)
    &__item {

        // width: 200px
        height: 100px;
        &:nth-child(3n+1) {
            background: blue url('https://www.planetware.com/wpimages/2020/02/france-in-pictures-beautiful-places-to-photograph-eiffel-tower.jpg') no-repeat center center/ cover; }
        &:nth-child(3n+2) {
            background: red url('https://ichef.bbci.co.uk/news/999/cpsprodpb/15951/production/_117310488_16.jpg') no-repeat center center/ cover; }
        &:nth-child(3n+3) {
            background: yellow url('https://www.planetware.com/wpimages/2019/10/switzerland-in-pictures-most-beautiful-places-matterhorn.jpg') no-repeat center center/ cover; } }
